import React from "react";
import PropTypes from "prop-types";


class Form extends React.Component {
  static propTypes = {
    getLoveScore: PropTypes.func
  };

  matchInput = e => {
    e.preventDefault();
   
    let name1 = e.target.elements.name.value;
    let name2 = e.target.elements.name_two.value;

    name1 = name1.replace(/\s+/g, "");
    name2 = name2.replace(/\s+/g, "");

     for (let i = 0; i < name1.length; i++) {
        for (let j = 0; j < name2.length; j++) {
            if (name1.charAt(i) === name2.charAt(j)) {
            name1 = name1.replace((name1.charAt(i)), "#");
            name2 = name2.replace((name2.charAt(j)), "#");
            }
        }
    }
    let result = name1 + name2;
    result = result.replaceAll("#", "");
    let resultLength = result.length;
    this.props.getLoveScore(resultLength);
    console.log(resultLength);
  };

  render() {
    return (
      <form onSubmit={this.matchInput}>
        <div className='row'>
          <label className='label'>
            <strong>Enter Name 1: </strong>
          </label>
          <input className='input' type="text" name="name" placeholder="Name" required />
         
        </div>

        <div className='row'>
          <label className='label'>
            <strong>Enter Name 2: </strong>
          </label>
          <input className='input' type="text" name="name_two" placeholder="Name" required />
        </div>
        <button className='button'>Match!</button>
      </form>
    );
  }
}

export default Form;
