import React from "react";
import PropTypes from "prop-types";


const flames = (n)=> {
  let match = 0;
  let arr = ['f','l','a','m','e','s']
  while(arr.length > 1){
       let c = n%arr.length;
       let index = c-1;
      if(index >= 0){
        let left = arr.slice(0,index);
        let right = arr.slice(index+1,arr.length+1);
      arr = right.concat(left);
      }else{
       arr = arr.slice(0,arr.length -1);
      }
      match=arr[0];
  }

    switch (match) {
        case 'f':
            console.log("friendship");
            break;
        case 'l':
            console.log("Lovers");
            break;
        case 'a':
            console.log("Affection");
            break;
        case 'm':
            console.log("Marriage");
            break;
        case 'e':
            console.log("Enemity");
            break;
        case 's':
            console.log("Siblings");
            break;
        default:
            console.log("FLAME Test works only for different names");
            break;
    }
    return match;
}








const getResult = (n)=> {
  const posibleAnswer = [
    "friend ",
    "lover ",
    "affection ",
    "marriage ",
    "enemy ",
    "sister"
  ];

  let filteredResult = posibleAnswer.filter(result => result[0] === n);
  return filteredResult;
  
}

const Flames = (props) => {
  const result = flames(props.loveScore);
  const resultDesc = getResult(result);

  if (props.loveScore === undefined) {
    return <p>&nbsp;</p>;
  } else {
    return (
      <div>
        <hr />
        <h3>
          {result.toUpperCase()} - {resultDesc}
        </h3>
      </div>
    );
  }
};

Flames.propTypes = {
  loveScore: PropTypes.number.isRequired
};

export default Flames;